import { useEffect, useState } from 'react';
import { Route, BrowserRouter as Router, Switch, Redirect } from 'react-router-dom';
import './App.css';

import firebase from 'firebase/app';
import Login from './containers/Login';
import Home from './containers/Home';
import BookingDetails from './containers/BookingDetails';
import BookingForm from './containers/BookingForm';
import Spinner from './components/Spinner';

function App(props) {
    const [isAppInitialized, setIsAppInitialized] = useState(false)
    const [loggedIn, setLoggedIn] = useState(null)
    useEffect(() => {
        if (!firebase.apps.length) {
            const firebaseConfig = {
                apiKey: "AIzaSyCuv5I89PWdNRygTZe0PrATsZGP8oQdjlA",
                authDomain: "host-4dfe7.firebaseapp.com",
                projectId: "host-4dfe7",
                storageBucket: "host-4dfe7.appspot.com",
                messagingSenderId: "884557550471",
                appId: "1:884557550471:web:4c1d09b8eab0655d557922",
                measurementId: "G-EREPYVX2HQ"
              };
            // Initialize Firebase
            firebase.initializeApp(firebaseConfig);
            firebase.analytics();
            firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL)
            .then(() => {
                setIsAppInitialized(true)
            })
            firebase.auth().onAuthStateChanged((user) => {
                if(user) {
                    setLoggedIn(true)
                } else {
                    setLoggedIn(false)
                }
            })
        }
    }, [])

    if (!isAppInitialized || loggedIn === null) {
        return (
            <div>
                <Spinner/>
            </div>
        )
    }

    if (!loggedIn) {
        return (
            <div className='App'>
                <Router>
                    <Switch>
                        <Route path='/' exact component={Login}></Route>
                        <Route path='/login' exact component={Login}></Route>
                        <Route render={() => <Redirect to="/" />} />
                    </Switch>
                </Router>
            </div>
        )
    }
    return (
        <div className='App'>
            <Router>
                <Switch>
                    <Route path='/bookingdetails' component={BookingDetails}></Route>
                    <Route path='/booking' component={BookingForm}></Route>
                    <Route render={() => <Redirect to='/bookingdetails'/>} />
                </Switch>
            </Router>
        </div>
    );
}


